    <el-carousel-item v-for="item in banner" :key="item.id">
      <el-image
          style="width: 100%;height:100%"
          :src="item.src"
          ref="imgHeight"
          oncontextmenu="return false"
          ondragstart="return false"
          fit="cover">
      </el-image>
    </el-carousel-item>
  </el-carousel>
  </div>
<!--    <div>-->
      <HomeBannerCar class="banner_car"></HomeBannerCar>
<!--    </div>-->
  </div>
</template>

<script>
import HomeBannerCar from "@/components/home/HomeBannerCar";
export default {
  components:{
    HomeBannerCar,
  },
  data() {
    return {
      banner:[
        {src:"https://www.itheima.com/images/slidead/HOMEPAGE/20230104110331%E9%A6%96%E9%A1%B5%E5%B9%BB%E7%81%AF1920-432.png"},
        {src:"https://www.itheima.com/images/slidead/HOMEPAGE/20230111113958%E4%BC%A0%E6%99%BA%E5%AE%98%E7%BD%911920_432.jpg"},
        {src:"https://www.itheima.com/images/slidead/HOMEPAGE/20230113172505%E9%A6%96%E9%A1%B5pc.jpg"},
      ],
    };
