    this.$refs.carousel.handleMouseEnter = () => { };
  },
  methods: {
    delHandleMouseEnter() {
      this.$refs.carousel.handleMouseEnter = () => { };
    },
    getBannerUrl(){
      this.$http({
        url:"http://rap2api.taobao.org/app/mock/309060/banner",
        method:'get',
      }).then(res=>{
        if (res.data.code=200){
          this.banner=res.data.data.banner
        }
      })
    },
  },
  created() {
    // this.getBannerUrl()
  }
}
</script>

<style scoped>
>>>.el-carousel__button {
  width: 15px;
  height: 15px;
