<template>
    <div ref="chart"  style="width: 600px;height:400px;"></div>
</template>

<script>
const echarts = require('echarts');
export default {
  name: "JavaEcharts",
  data () {
    return {};
  },
  methods: {
    initCharts () {
      let myChart = echarts.init(this.$refs.chart);

      // 绘制图表
      myChart.setOption({
        tooltip: {
          trigger: 'item',
          triggerOn: 'mousemove'
        },
        series: [
          {
            type: 'tree',
            data: [{
              "name": "后端",
              "children": [
                {
                  "name": "编程基础",
                  "children": [
                    {
                      "name": "Java语言",
                      "children": [
                        {"name": "基础语法",},
                        {"name": "面向对象",},
                        {"name": "接口",},
                        {"name": "容器",},
                        {"name": "异常",},
                      ]
                    },
                    {
                      "name": "数据结构",
                      "children": [
                        {"name": "字符串",},
                        {"name": "数组",},
                        {"name": "链表",},
                        {"name": "二叉树",},
                        {"name": "哈希",},
                      ]
                    },
                    {
                      "name": "计算机网络",
                      "children": [
                        {"name": "ARP协议",},
                        {"name": "IP/ICMP协议",},
                        {"name": "Session/Cookie",},
                      ]
                    },
                    {
                      "name": "操作系统",
                      "children": [
                        {"name": "进程/线程",},
                        {"name": "并发/锁",},
                        {"name": "内存管理和调度",},
                        {"name": "I/O原理",},
                      ]
                    },
                    {
                      "name": "数据库",
                      "children": [
                        {"name": "SQL语句书写",},
                        {"name": "事务与隔离级别",},
                        {"name": "索引",},
                        {"name": "锁",},
                      ]
                    }
                  ]
                },
                {
                  "name": "研发工具",
                  "children": [
                    {"name": "集成开发环境",
                      "children": [
                        {"name": "Eclipse",},
                        {"name": "IDEA",},
                        {"name": "VS Code",},
                      ]},
                    {"name": "Linux系统",
                      "children": [
                        {"name": "Linux常用命令",},
                        {"name": "基本Shell脚本",},
                      ]}
                  ]
                },
                {
                  "name": "应用框架学习",
                  "children":[
                    {"name":"Spring全家桶",
                      "children": [
                        {"name": "Spring", },
                        {"name": "Spring MVC", },
                        {"name": "Spring Boot", }]
                    },
                    {"name":"服务器软件",
                      "children": [
                        {"name": "web服务器",
                          "children":[
                            {"name": "Nginx", },
                          ]},
                        {"name": "应用服务器",
                          "children":[
                            {"name": "Tomcat", },
                            {"name": "Jetty", },
                            {"name": "Undertow", },
                          ]},]
                    },
                  ]},
              ]
            }],
            top: '5%',
            left: '7%',
            bottom: '1%',
            right: '40%',
            symbolSize: 13,
            label: {
              position: 'left',
              verticalAlign: 'middle',
              align: 'right',
              fontSize: 13
            },
            leaves: {
              label: {
                position: 'right',
                verticalAlign: 'middle',
                align: 'left'
              }
            },
            emphasis: {
              focus: 'descendant'
            },
            expandAndCollapse: true,
            animationDuration: 550,
            animationDurationUpdate: 750
          }
        ]
      });
    }
  },
  mounted () {
    this.initCharts();
  }
}
</script>

