<template>
  <div>
    <div style="text-align: left">
      <span class="my_title" >
        <i class='bx bx-trip' style='color:#601495;'></i>
        热门职业规划路线</span>
    </div>
    <div class="my_content">
      <el-tabs type="border-card">
        <el-tab-pane :lazy="true">
          <span slot="label"><i class='bx bxl-edge' ></i>前端工程师</span>
          <el-row>
            <el-col :span="11">
              <el-card shadow="hover">
                <WebEcharts></WebEcharts>
              </el-card>
            </el-col>
            <el-col :span="11" :offset="2">
              <el-card shadow="hover">
                <WebRoseEcharts></WebRoseEcharts>
              </el-card>
            </el-col>
          </el-row>
        </el-tab-pane>
        <el-tab-pane :lazy="true">
          <span slot="label"><i class='bx bxl-java'></i>Java后端开发</span>
            <el-row>
              <el-col :span="11">
                <el-card shadow="hover">
                  <JavaEcharts></JavaEcharts>
                </el-card>
              </el-col>
              <el-col :span="11" :offset="2">
                <el-card shadow="hover">
                  <JavaYearEcharts></JavaYearEcharts>
                </el-card>
              </el-col>
            </el-row>
        </el-tab-pane>
        <el-tab-pane :lazy="true">
          <span slot="label"><i class='bx bx-user-plus' ></i>更多职业</span>
          <el-row>
            <el-col :span="10" :offset="2">
            <TimeEcharts></TimeEcharts>
            </el-col>
            <el-col :span="12" >
              <div style="text-align: left;padding-top: 160px;height: 100%">
                <span class="my_tip">后台小编正在加班加点为您整理资料</span><br>
                <span class="my_tip" style="padding-left: 50px">请您耐心等待······</span><br>
                <div style="padding-top: 40px;padding-left: 150px">
                  <el-button
                      type="success"
                      @click="open"
                      :disabled="isLock">再催一下</el-button>
                </div>
              </div>
            </el-col>
          </el-row>

        </el-tab-pane>
      </el-tabs>
    </div>
  </div>
</template>

<script>
import WebEcharts from "@/components/echarts/WebEcharts";
import JavaEcharts from "@/components/echarts/JavaEcharts";
import WebRoseEcharts from "@/components/echarts/WebRoseEcharts";
import JavaYearEcharts from "@/components/echarts/JavaYearEcharts";
import TimeEcharts from "@/components/echarts/TimeEcharts";
export default {
  name: "HomeEchartsTabs",
  components:{
    WebEcharts,
    JavaEcharts,
    WebRoseEcharts,
    JavaYearEcharts,
    TimeEcharts,
  },
  data() {
    return {
      isLock:false,
    };
  },
  methods:{
    open() {
      this.isLock = true
      this.$message({
        message: '已收到您的催促，内容正在快马加鞭赶来！',
        type: 'success'
      });
      setTimeout(() => {
        this.isLock = false
      }, 5000)
    },
  },
}
</script>

<style scoped>
.my_title{
  font-size: 30px;
}
.my_content{
  padding-top: 20px;
}
.my_tip{
  font-family:Hiragino Sans GB,fangsong;
  font-size: 25px;
  font-weight: lighter;
}
</style>