<template>
  <div ref="chart"  style="width: 500px;height:400px;"></div>
</template>

<script>
const echarts = require('echarts');
export default {
  name: "JavaYearEcharts",
  data () {
    return {};
  },
  methods: {
    initCharts () {
      let myChart = echarts.init(this.$refs.chart);
      myChart.setOption(
          {
            title: {
              text: '后端金字塔',
              left:"center"
            },
            tooltip: {
              trigger: 'item',
              formatter: '{a} <br/>{b} : {d}%'
            },
            legend: {
              data: ['SpringBoot', '后端框架', 'JavaWeb', '数据库', 'Java基础'],
              top: 'bottom',
            },
            series: [
              {
                name: 'Funnel',
                type: 'funnel',
                sort: 'ascending',
                left: '10%',
                top: 60,
                bottom: 60,
                width: '80%',
                min: 0,
                max: 100,
                minSize: '0%',
                maxSize: '100%',
                gap: 2,
                label: {
                  show: true,
                  position: 'inside'
                },
                labelLine: {
                  length: 10,
                  lineStyle: {
                    width: 1,
                    type: 'solid'
                  }
                },
                itemStyle: {
                  borderColor: '#fff',
                  borderWidth: 1
                },
                emphasis: {
                  label: {
                    fontSize: 20
                  }
                },
                data: [
                  { value: 20, name: 'SpringBoot'},
                  { value: 40, name: '后端框架'},
                  { value: 60, name: 'JavaWeb'},
                  { value: 80, name: '数据库'},
                  { value: 100, name: 'Java基础'}
                ]
              }
            ]
          }
      )

    }
  },
  mounted() {
    this.initCharts();
  }
}
</script>

