<template>
  <el-descriptions class="margin-top"  title="个人基本信息编辑" :column="3"  border>
    <template slot="extra">
      <el-button type="primary"  v-show="lock" @click="active()">编辑</el-button>
      <el-button type="success"  v-show="!lock" @click="save()">保存</el-button>
    </template>
    <el-descriptions-item>
      <template slot="label">
        <i class="el-icon-user"></i>
        姓名
      </template>
      <el-input
          v-model="user.name"
          :disabled="lock"
          >
      </el-input>
    </el-descriptions-item>
    <el-descriptions-item>
      <template slot="label">
        <i class="el-icon-male"></i>
        性别
      </template>
      <el-select v-model="user.sex" placeholder="请选择" :disabled="lock">
        <el-option
            v-for="item in sexs"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          <span style="float: left">{{ item.label }}</span>
        </el-option>
      </el-select>
    </el-descriptions-item>
    <el-descriptions-item>
      <template slot="label">
        <i class='el-icon-time'></i>
        出生日期
      </template>
      <el-date-picker
          v-model="user.birth"
          align="right"
          type="date"
          placeholder="选择日期"
          :disabled="lock"
          format="yyyy 年 MM 月 dd 日"
          value-format="yyyy-MM-dd">
      </el-date-picker>
    </el-descriptions-item>
    <el-descriptions-item>
      <template slot="label">
        <i class="el-icon-mobile-phone"></i>
        手机号
      </template>
      <el-input
          v-model="user.phone"
          :disabled="lock">
      </el-input>
    </el-descriptions-item>
    <el-descriptions-item>
      <template slot="label">
        <i class="el-icon-message"></i>
        邮箱
      </template>
      <el-input
          v-model="user.email"
          :disabled="lock">
      </el-input>
    </el-descriptions-item>
    <el-descriptions-item>
      <template slot="label">
        <i class="el-icon-location-outline"></i>
        居住地
      </template>
      <el-select v-model="user.city" placeholder="请选择" :disabled="lock">
        <el-option
            v-for="item in cities"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          <span style="float: left">{{ item.label }}</span>
        </el-option>
      </el-select>
    </el-descriptions-item>
    <el-descriptions-item>
      <template slot="label">
        <i class="el-icon-school"></i>
        学校
      </template>
      <el-select v-model="user.school" placeholder="请选择" :disabled="lock">
      <el-option
          v-for="item in school"
          :key="item.value"
          :label="item.label"
          :value="item.value">
        <span style="float: left">{{ item.label }}</span>
      </el-option>
      </el-select>
    </el-descriptions-item>
    <el-descriptions-item>
      <template slot="label">
        <i class="el-icon-postcard"></i>
        专业
      </template>
      <el-input
          v-model="user.major"
          :disabled="lock">
      </el-input>
    </el-descriptions-item>
    <el-descriptions-item>
      <template slot="label">
        <i class="el-icon-reading"></i>
        学历
      </template>
      <el-select v-model="user.education" placeholder="请选择" :disabled="lock">
        <el-option
            v-for="item in educations"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          <span style="float: left">{{ item.label }}</span>
        </el-option>
      </el-select>
    </el-descriptions-item>
    <el-descriptions-item>
      <template slot="label">
        <i class="el-icon-timer"></i>
        就读时间
      </template>
      <el-date-picker
          v-model="user.educationTime"
          :disabled="lock"
          format="yyyy 年 MM 月"
          value-format="yyyy-MM"
          type="monthrange"
          range-separator="至"
          start-placeholder="开始月份"
          end-placeholder="结束月份">
      </el-date-picker>
    </el-descriptions-item>
    <el-descriptions-item>
      <template slot="label">
        <i class="el-icon-office-building"></i>
        联系地址
      </template>
      <el-input
          v-model="user.address"
          :disabled="lock">
      </el-input>
    </el-descriptions-item>
  </el-descriptions>
</template>

<script>
export default {
  name: "PersonalData",
  data() {
    return {
      lock:true,
      user:{
        name:"大猫灰",
        sex:"男",
        phone:"11192929",
        email:"277282@qq.com",
        birth:"2010-10-1",
        city:"南宁",
        school:"广西大学",
        major:"计算机科学与技术",
        education:"本科",
        educationTime:["{2019-9-1}","{2023-6-1}"],
        address:"江苏省苏州市吴中区吴中大道 1188 号",
      },
      cities: [{
        value: '北京',
        label: '北京'
      }, {
        value: '上海',
        label: '上海'
      }, {
        value: '南京',
        label: '南京'
      }, {
        value: '成都',
        label: '成都'
      }, {
        value: '深圳',
        label: '深圳'
      }, {
        value: '广州',
        label: '广州'
      }],
      school:[{
        value: '北京大学',
        label: '北京大学'
      },{
        value: '清华大学',
        label: '清华大学'
      }],
      sexs:[{
        value: '男',
        label: '男'
      },{
        value: '女',
        label: '女'
      },],
      educations:[{
        value: '大专',
        label: '大专'
      },{
        value: '本科',
        label: '本科'
      },{
        value: '研究生',
        label: '研究生'
      }],
    };
    },
  methods:{
    save(){
      console.log(this.user)
      this.lock=!this.lock
    },
    active(){
      this.lock=!this.lock
    },
    getUserInfo(){
      this.$http({
        url:"http://rap2api.taobao.org/app/mock/309060/user",
        method:'get',
      }).then(res=>{
        if (res.data.code=200){
          this.user=res.data.data.user
        }
      })
    },
  },
  created() {
    // this.getUserInfo()
  }
}
</script>

