<template>
  <div ref="chart"  style="width: 500px;height:400px;"></div>
</template>

<script>
const echarts = require('echarts');
export default {
  name: "WebRoseEcharts",
  data () {
    return {};
  },
  methods: {
    initCharts () {
      let myChart = echarts.init(this.$refs.chart);

      // 绘制图表
      myChart.setOption({
            legend: {
              top: 'bottom',
              padding: [0, 10]
            },
            title: {
              text: '前端框架使用占比',
              subtext: '截止至2022年',
              left: 'center'
            },
            tooltip: {
              trigger: 'item',
              formatter: '{a} <br/>{b} : {d}%'
            },
            series: [
              {
                name: '使用率',
                type: 'pie',
                radius: [25, 125],
                center: ['50%', '50%'],
                roseType: 'area',
                itemStyle: {
                  borderRadius: 8
                },
                data: [
                  { value: 40, name: 'React' },
                  { value: 38, name: 'Vue' },
                  { value: 32, name: 'Angular' },
                  { value: 30, name: 'Backbone ' },
                  { value: 28, name: 'Inferno' },
                  { value: 22, name: '其他' }
                ]
              }
            ]
          }
      )
    }
  },
  mounted () {
    this.initCharts();
  }
}
</script>

