<template>
    <div ref="chart"  style="width: 600px;height:400px;"></div>
</template>

<script>
const echarts = require('echarts');
export default {
  name: "WebEcharts",
  data () {
    return {};
  },
  methods: {
    initCharts () {
      let myChart = echarts.init(this.$refs.chart);

      // 绘制图表
      myChart.setOption({
        tooltip: {
          trigger: 'item',
          triggerOn: 'mousemove'
        },
        series: [
          {
            type: 'tree',
            data: [{
              "name": "前端",
              "children": [
                {
                  "name": "网络知识",
                  "children": [
                    {
                      "name": "网络工作原理",
                    },
                    {
                      "name": "HTTP协议",
                    },
                    {
                      "name": "域名相关知识",
                    }
                  ]
                },
                {
                  "name": "超文本标记语言",
                  "children": [
                    {"name": "HTML基础知识",},
                    {"name": "表单和验证",}
                  ]
                },
                {
                  "name": "层叠样式表",
                  "children": [
                    {"name": "CSS基础知识", },
                    {"name": "页面切图和布局", },

                  ]
                },
                {
                  "name": "浏览器脚本语言",
                  "children": [
                    {"name": "JavaScript基础语法",},
                    {"name": "使用JavaScript操作DOM"},
                    {"name": "Ajax异步请求",}
                  ]
                },
                {
                  "name": "包管理工具",
                  "children": [
                    {"name": "npm"},
                    {"name": "yarn",},
                  ]
                },
                {
                  "name": "前端三大框架",
                  "children": [
                    {"name": "React.js",},
                    {"name": "Vue.js",},
                    {"name": "Angular",},
                  ]
                }
              ]
            }],
            top: '5%',
            left: '7%',
            bottom: '1%',
            right: '40%',
            symbolSize: 13,
            label: {
              position: 'left',
              verticalAlign: 'middle',
              align: 'right',
              fontSize: 13
            },
            leaves: {
              label: {
                position: 'right',
                verticalAlign: 'middle',
                align: 'left'
              }
            },
            emphasis: {
              focus: 'descendant'
            },
            expandAndCollapse: true,
            animationDuration: 550,
            animationDurationUpdate: 750
          }
        ]
      });
      myChart.on('click', function(params) {
      });
    }
  },
  mounted () {
    this.initCharts();
  }
}
</script>

